export class AbstractUserRepresentation {
    company?: string;
    email?: string;
    externalId?: string;
    firstName?: string;
    id?: number;
    lastName?: string;
    pictureId?: number;

    constructor(input?: any) {

        if (input) {
            Object.assign(this, input);
        }
    }

}
